import Vue from 'vue'
import Reduce from './Reduce'
import router from './router'

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<cape-reduce/>',
  components: {
    'cape-reduce': Reduce
  }
})



